@media screen and ( max-width: 578px ) {
    .gifts {
        text-align: center;
        &__container {
            display: flex;
            flex-wrap: wrap;

            &__block {
                margin: 30px 0; } } }


    .adds {

        &__info, &__buttons {
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            justify-content: center;

            margin: 0;

            text-align: center;
            & a {
                margin: 15px 0; } } }

    .blogs {
        width: 90%;
        &__block-1 {
            flex-direction: column;
            flex-wrap: wrap;

            margin: 0;
            &__info {
                width: 100%;

                padding: 15px;
                margin: 0;
                & h2 {
                    font-size: 1.3em; }
                & p, & a, & h2 {
                    margin-top: 30px; }

                & a {
                    font-size: .8em; } } }

        &__container-block-2 {
            flex-wrap: wrap; }
        &__block-2 {
            padding: 30px 0; } } }
